.Toolbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #AA272F;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}
.Toolbar nav {
    height: 100%;
}

@media (max-width: 499px) {
    .Toolbar nav {
        display: none;
    }
}
