.Order {
    width: 80%;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    margin: 10px auto;
    box-sizing: border-box;
    text-align: left;
    position: relative;
}
.Order span {
    padding:0 5px;
}
.HighlightOffIcon {
    position: absolute;
    top:5px;
    right: 5px;
}